import React from 'react'
import { Modal, ModalBody, Text, Image, Button, Link, OpenNewIcon } from '@ricefarm/uikitv2'
import { BASE_ADD_LIQUIDITY_URL, BASE_V1_ADD_LIQUIDITY_URL } from 'config'
import { Token } from 'config/constants/types'
import { useTranslation } from 'contexts/Localization'

interface Props {
  currency: Token
  onDismiss?: () => void
}

const GetLpModal: React.FC<Partial<Props>> = ({ currency, onDismiss }) => {
  const { t } = useTranslation()
  const ADD_RICE_LIQUIDITY = `${BASE_ADD_LIQUIDITY_URL}/BNB/0xAf734D39312c19e1C541E9c5BfBC4A6Ea5892746`
  const ADD_TS_LIQUIDITY = `${BASE_V1_ADD_LIQUIDITY_URL}/BNB/0x3504de9e61FDFf2Fc70f5cC8a6D1Ee493434C1Aa`
  return (
    <Modal title={t('LP Tokens required')} onDismiss={onDismiss}>
      <ModalBody maxWidth="288px">
        <Image
          src={`/images/farms/${currency.symbol.split(' ')[0].toLocaleLowerCase()}.svg`}
          width={72}
          height={72}
          margin="auto"
          mb="24px"
        />
        <Text mb="16px">{t(`Youll need ${currency.symbol} tokens to participate in the IFO!`)}</Text>
        <Text mb="24px">{t('Get LP tokens, or make sure your tokens arent staked somewhere else.')}</Text>
        <Button
          as={Link}
          external
          href={currency.symbol === 'RICE-BNB LP' ? ADD_RICE_LIQUIDITY : ADD_TS_LIQUIDITY}
          endIcon={<OpenNewIcon color="white" />}
          minWidth="100%" // Bypass the width="fit-content" on Links
        >
          {t('Get LP tokens')}
        </Button>
      </ModalBody>
    </Modal>
  )
}

export default GetLpModal
